	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Allison Kaptur\n\nHave you ever wondered how the CPython interpreter works? Do you know where to find a 1,500 line switch statement in CPython? I'll talk about the structure of the interpreter that we all use every day by explaining how Ned Batchelder and I chased down a mysterious bug in Byterun, a Python interpreter written in Python. We'll also see visualizations of the VM as it executes your code.\n\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT34M33S"},"presenter":{"name":null},"slug":"allison-kaptur-bytes-in-the-machine-inside-the-cpython-interpreter-pycon-2015","source":{"id":"HVUTjQzESeo","provider":"youtube"},"tag":[],"title":"Allison Kaptur - Bytes in the Machine: Inside the CPython interpreter - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"allison-kaptur-bytes-in-the-machine-inside-the-cpython-interpreter-pycon-2015","tags":[]}}

